          </mu-paper>
          </li>   
      </ul> 
       <!--<mu-paper class="demo-paper" :zDepth="2" >
            
      </mu-paper>-->
  </div>
  
</template>

<script>
// import IMG from '../../static/img/banner.jpg'
import myron from '../../static/img/1.jpg'
export default {
  name: 'hottheme',
  data () {
    return {
      item: '1',
      myron

    }
  },
  mounted () {
    var url = null
    this.$store.dispatch('setLoad', true)
    url = '/api/4/news/'
    this.$http.post(url, {id: 'latest'}, {progress: function (event) {
   
    }
    }).then(function (res) {
      this.item = res.body
      this.$store.dispatch('setLoad', false)
    }, function (res) {
      console.log(res)
    })
    window.addEventListener('onmousewheel ', function () {
      console.log(1)
    })
  },
// winWidth = document.body.clientWidth;
  computed: {

  },
  methods: {

  },
  updated () {

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->

<style scoped>
@import '/static/style/theme.css';

</style>



