<template>
  <div class="login">
    <div >
        <mu-tabs class="login-head"  value="activeTab" @change="handleTabChange">
            <mu-tab :class="activeTab=='tab1'? 'bacactive' :'bacFrozen'"  value="tab1"><span class="titlesize">登陆</span></mu-tab>
            <mu-tab :class="activeTab=='tab2'? 'bacactive' :'bacFrozen'"  value="tab2" ><span class="titlesize">注册</span></mu-tab>
        </mu-tabs>
    </div>
    <div class="login-content">
        <div v-if="activeTab === 'tab1'">
          <ul>
            <li><mu-text-field hintText="用户名" v-model="zhihuL.user" type="text" icon="group" fullWidth/></li>
             <li>  <mu-text-field hintText="密码" type="password" v-model="zhihuL.pwd" icon="vpn_key" fullWidth/></li>
              <li> <mu-radio label="记住密码" name="group" v-model="zhihuL.remember" nativeValue="simple1"  class="demo-radio"/></li>
              <li>
                 <!--<mu-raised-button label="登陆" @click='login' fullWidth  primary/>-->
                 <mu-raised-button  label="登陆" @click="login('top')" fullWidth  primary/>
              </li>
          </ul>
         <br/>         
        </div>
        <div v-if="activeTab === 'tab2'">
            <ul>
            <li><mu-text-field hintText="用户名" v-model="zhihuR.user" type="text" icon="group" fullWidth/></li>
             <li>  <mu-text-field hintText="密码" v-model="zhihuR.pwd" type="password" icon="vpn_key" fullWidth/></li>
    
              <li>
                 <mu-raised-button label="注册" @click='register("top")' primary fullWidth/>
              </li>
          </ul>
        </div>
    </div>
    <footer>
  <mu-raised-button  label="回到首页-游客模式"   icon="home" @click='goback' fullWidth  secondary/>
    </footer>
     <mu-popup :overlayColor='overlaycolor' position="top" :overlay="false" popupClass="demo-popup-top" :open="topPopup">
    {{msg}}
  </mu-popup>
</div>
 


</div>
</template>

<script>
// import store from '../vuex/store.js'
export default {
  name: 'hello',
  data () {
    return {
      activeTab: 'tab1',
      zhihuL: {
        user: '',
        pws: '',
        remember: 'false'
      },
      zhihuR: {
        user: '',
        pws: ''
      },
      overlaycolor: '#32CD32',
      topPopup: false,
      msg: ''
    }
  },
  watch: {
    topPopup (val) {
      if (val) {
        setTimeout(() => {
          this.topPopup = false
        }, 2000)
      }
    }
  },
  methods: {
    handleTabChange (val) {
      console.log(val, 'val')
      this.activeTab = val
    },
    login (position) {
      if (this.zhihuL.user == '' && this.zhihuL.pwd == '') {
        console.log('用户名密码不能为空')
        return
      }
      let options = {
        user: this.zhihuL.user,
        pwd: this.zhihuL.pwd
      }
      console.log(options, 'l')
      let _this = this
      this.$http.post('/login', options, {
        progress: function (event) {
        }
      }).then(function (res) {
        console.log(res.body)
        if (res.body.code === '1') {
          _this.msg = res.body.msg
          _this[position + 'Popup'] = true
          // _this.overlaycolor = '#32CD32'
          _this.$store.dispatch('seteditor', res.body)
          setTimeout(function () {
            _this.$router.go(-1)
            _this.zhihuL.pwd = ''
          }, 5000)
        } else {
          _this.msg = res.body.msg
          _this[position + 'Popup'] = true
          // _this.overlaycolor = '#DC143C'
        }
      }, function (res) {
        console.log(res)
      })
    },

    register (position) {
      if (this.zhihuR.user == '' && this.zhihuR.pwd == '') {
        console.log('用户名密码不能为空')
        return
      }
      let options = {
        user: this.zhihuR.user,
        pwd: this.zhihuR.pwd
      }
      console.log(options, 'r')
      let _this = this
      this.$http.post('/register', options).then(function (res) {
        // this.activeTab = 'tab1'
        // 失败 继续登陆
        _this[position + 'Popup'] = true
        if (res.body.code == '0') {
          // 用户名已经存在
          _this.msg = res.body.msg
        } else if (res.body.code == '2') {
          // 服务器错误
          _this.msg = res.body.msg
        } else if (res.body.code == '1') {
          // 注册成功
          _this.msg = res.body.msg
          _this.activeTab = 'tab1'
        }

        this.zhihuL.user = this.zhihuR.user
      }, function (err) {
        console.log(err)
      })
    },
    goback () {
      this.$router.go(-1)
    }
  }
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.login{
width: 100%;
height: 100%;
  
}
.titlesize{
  font-size: 20px;
}
.login-head{
  height: 50px;
}
.login-content{
  margin-top: 30px;
}
.login-content ul{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.login-content ul li {
  width: 60%;
  margin-top: 20px;
  list-style: none;
}
.login footer{
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}
.demo-popup-top {
  width: 100%;
  opacity: .8;
  height: 48px;
  line-height: 48px;
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 375px;
}
.bacFrozen{
  background-color: #03a9f4;
}
.bacactive{
  background-color: #ff4081;
}
</style>



