<template>
  <div class="home">
    <header class="home-head">
      <mu-appbar >
        <mu-icon-button icon='menu' @click="toggle(true)" slot="left" />
            <span>{{title}}</span>
           <Prompt title="收藏" :disabled='Boolean(false)' :value="add"  @click='a' slot="right"/>
        
      </mu-appbar>
      
    </header>
    <section>
      <aside>
        <!-- 
        <mu-raised-button label="toggle drawer" @click="toggle()"/>
        <mu-raised-button label="undocked drawer" @click="toggle(true)"/>
         -->

        <mu-drawer :open="open" :docked="docked" @close="toggle()">
          <mu-list @itemClick="docked ? '' : toggle()">  
            <mu-list-item title="图片" :disabled="false" >
              <mu-avatar slot="left" :src="myron" />
            </mu-list-item>
            <router-link :to="{ name:'hottheme',params:{id:'latest'}} ">
                 <mu-flat-button label="首页"  icon="home" primary/>
            </router-link>
            <ul>
              <li class="home-li"  v-for="item in list" :key="item.id"> 
                <mu-paper :zDepth="2" class="home-paper">
                 <div class="home-title">
                    <router-link :to="{ name:'theme',params:{id:item.id}}">
                     <mu-flat-button :color="item.id == activethemeid ? 'red': 'grey'" :label="item.name"   icon="home" primary/>
                     </router-link>
                  </div>
                  <div class="home-collect">
                    <!--<mu-icon value="add" />-->
                    <Prompt title="收藏" :disabled='show' :value="'add-'+item.id" @click='a(item.id)'/>
                    </div>
                </mu-paper>
                 
              </li>
            </ul>
          </mu-list>
        </mu-drawer>
      </aside>
      <router-view class="view"></router-view>
    </section>
     <div class="loading" v-if="load" >
       <div class="load-inner">
           <p>Loading</p>
          <mu-linear-progress :size='Number(5)'  color="red"/>
       </div>
      
      </div>
    
  </div>
</template>


<script>
import myron from '../../static/img/1.jpg'
import Prompt from './util/Prompt'
export default {
  name: 'home',
  data () {
    return {
      list: '',
      open: false,
      docked: true,
      myron,
      hottheme: '',
      iconclass: 'home-icon',
      add: 'add',
      show: true,
      c: true,
      item: ''

    }
  },
  components: {
    Prompt
  },
  mounted () {
    this.$http.get('/api/4/themes', {
      progress: function (event) {

      }
    }).then(function (res) {
      this.list = res.body.others
      this.$store.dispatch('setLoad', false)
    }, function (res) {
      console.log(res, '超时')
    })
  },
  beforeRouteEnter (to, from, next) {
    console.log('beforeRouteEnter')
    next()
  },
  beforeRouteUpdate (to, from, next) {
  //  this.title =
    this.toggle()
    if (to.name === 'hottheme') {
      this.$store.dispatch('settitle', '首页')
      return
    }
    for (let item of this.list) {
      if (to.path.split('/')[2] == item.id) {
        this.$store.dispatch('settitle', item.name)
        this.$store.dispatch('activetheme', item.id)
        break
      }
    }

    next()
  },
  beforeRouteLeave (route, redirect, next) {
    next()
  },
  computed: {
    activethemeid () {
      return this.$store.state.activethemeid
    },
    load () {
      return this.$store.state.load
    },
    title () {
      return this.$store.state.title
    }

  },
  methods: {
    toggle (flag) {
      this.open = !this.open
      this.docked = !flag
    },
    collect () {

    },
    a (value) {
      console.log(value, 'home')
      if (this.c) {
        this.add = 'remove'
        this.c = false
        this.show = false
      } else {
        this.add = 'add'
        this.c = true
        this.show = true
      }
    }
  }
}

</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->

<style scoped>
  @import '/static/style/home.css';

</style>



